<template>
  <Layout-box one="店铺绑定主播">
    <el-button size="small" @click="$router.push('/live/addShopZhuBo')">新增主播</el-button>
    <table-cmp :tableData="tableData" :tableColumn="tableColumn" @delete="childDelete" />
    <!-- 分页 -->
    <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
  </Layout-box>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import tableCmp from '@/components/table_cmp/index.vue'

export default {
  name: 'shop_zhu_bo',

  data () {
    return {
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      tableColumn: [
        { prop: 'anchor', label: '姓名', type: 'text', formatter: 'shopZhuBoAnchorFormatter' },
        { prop: 'status', label: '状态', type: 'text', formatter: 'shopZhuBoStatusFormatter' },
        { label: '操作', type: 'button', btnGroup: [
          { name: '解绑', value: 'delete' }
        ] },
      ],
    }
  },

  components: {
    LayoutBox,
    tableCmp,
  },

  async created () {
    await this.apiQueryAnchor()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 点击解绑主播
    async childDelete (e) {
      const res = await this.$http.get(`${this.$api.deleteAnchorShop}/${e.row.id}`)
      if (res.data.success) {
        this.$message.success('删除成功')
        await this.apiQueryAnchor()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // -----------------------------------------------------------
    // 主播注册申请列表查询
    async apiQueryAnchor () {
      const data = {
        ...this.queryInit
      }
      const res = await this.$http.post(this.$api.findAnchorShop, data)
      this.tableData = res.data.data.list
      this.queryInit = res.data.data
    },
  }
}
</script>

